.container {
  max-width: 1230px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  position: relative;
  background-color: #1E2433;
  box-shadow: 0px 4px 10px rgba(14,18,28,0.25);
}

.nav {
  padding: 40px 5px;
}

.nav__logo {
  flex: 1 0 auto;
}

.nav__popup {
  position: absolute;
  background-color: #000;
  top: 100%;
  left: 0;
  width: 100vw;
}

.nav__link {
  text-transform: uppercase;
  color: #fff;
  display: block;
  text-align: center;
  padding: 10px 0;
  font-size: .8rem;
}

.nav__link:hover {
  color: #46C7A0;
}

.js .nav__popup {
  max-height: 0px;
  overflow: hidden;
  transition: all 0.3s ease-in;
}

.js .nav__popup.open {
  max-height: 1000px;
}

.js .nav__popup.open .nav__list {
  text-align: center;
}

.navbar__btn,
.navbar__svg {
  width: 24px;
  height: 24px;
}

.navbar__btn {
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  border-radius: 0;
  cursor: pointer;
  appearance: none;
  display: inline-block;
}

.navbar__svg {
  width: 100%;
}

.menu-button {
  border: none;
  padding: 4px 5px;
  background: transparent;
  display: none;
  position: absolute;
  right: 25px;
  top: 35px;
  z-index: 10;
}

.menu-button.open span:first-child {
  transform: rotate(45deg) translate(8px, 8px);
}

.menu-button.open span:nth-child(2) {
  display: none;
}

.menu-button.open span:nth-child(3) {
  transform: rotate(-45deg);
}

.js .menu-button {
  display: block;
}

.menu-bar {
  display: block;
  width: 35px;
  height: 4px;
  background-color: white;
  border-radius: 2px;
  margin: 6px 0;
  transition: transform 0.2s;
}

.control {
  display: block;
  position: relative;
  padding-left: 30px;
  margin-bottom: 15px;
  cursor: pointer;
}

.control input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.control__indicator {
  position: absolute;
  top: 0px;
  left: 0;
  height: 16px;
  width: 16px;
  background: #fff;
  border: 1px solid #ccc;
}

.control--checkbox .control__indicator {
  border-radius: 3px;
}

.control:hover input:not([disabled]) ~ .control__indicator,
.control input:focus ~ .control__indicator {
  border-color: #666;
}

.control input:checked ~ .control__indicator {
  background: #fff;
}

.control input:disabled ~ .control__indicator {
  background: #e6e6e6;
  opacity: 0.6;
  pointer-events: none;
}

.control__indicator:after {
  content: '';
  position: absolute;
  display: none;
}

.control input:checked ~ .control__indicator:after {
  display: block;
}

.control--checkbox .control__indicator:after {
  left: 5px;
  top: 0px;
  width: 5px;
  height: 12px;
  border: solid #34bb92;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.control--checkbox input:disabled ~ .control__indicator:after {
  border-color: #7b7b7b;
}

.control {
  display: block;
  position: relative;
  padding-left: 30px;
  margin-bottom: 15px;
  cursor: pointer;
}

.control input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.control__indicator {
  position: absolute;
  top: 0px;
  left: 0;
  height: 16px;
  width: 16px;
  background: #fff;
  border: 1px solid #ccc;
}

.control--radio .control__indicator {
  border-radius: 50%;
}

.control:hover input:not([disabled]) ~ .control__indicator,
.control input:focus ~ .control__indicator {
  border-color: #666;
}

.control input:checked ~ .control__indicator {
  background: #fff;
}

.control input:disabled ~ .control__indicator {
  background: #e6e6e6;
  opacity: 0.6;
  pointer-events: none;
}

.control__indicator:after {
  content: '';
  position: absolute;
  display: none;
}

.control input:checked ~ .control__indicator:after {
  display: block;
}

.control--radio .control__indicator:after {
  left: 5px;
  top: 5px;
  height: 6px;
  width: 6px;
  border-radius: 50%;
  background: #34bb92;
}

.control--radio input:disabled ~ .control__indicator:after {
  background: #7b7b7b;
}

.select {
  position: relative;
  display: inline-block;
  margin-bottom: 15px;
  width: 100%;
}

.select select {
  display: inline-block;
  width: 100%;
  cursor: pointer;
  padding: 10px 15px;
  outline: 0;
  border: 0;
  border-radius: 0;
  background: #e6e6e6;
  color: #7b7b7b;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.select select::-ms-expand {
  display: none;
}

.select select:hover,
.select select:focus {
  color: #000;
  background: #ccc;
}

.select select:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.select__arrow {
  position: absolute;
  top: 16px;
  right: 15px;
  width: 0;
  height: 0;
  pointer-events: none;
  border-style: solid;
  border-width: 8px 5px 0 5px;
  border-color: #7b7b7b transparent transparent transparent;
}

.select select:hover ~ .select__arrow,
.select select:focus ~ .select__arrow {
  border-top-color: #000;
}

.select select:disabled ~ .select__arrow {
  border-top-color: #ccc;
}

@media (min-width: 360px) {
  .nav__link {
    font-size: calc( 12.8px + (16 - 12.8) * ((100vw - 360px) / (1024 - 360)));
  }
}

@media only screen and (min-width: 769px) {
  .nav {
    display: flex;
    align-items: center;
  }

  .nav__popup {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 2 0 auto;
  }

  .nav__item:not(:last-child) {
    margin-right: 28px;
  }

  .navbar {
    display: flex;
  }
}

@media only screen and (min-width: 993px) {
  .nav__list {
    display: flex;
  }

  .js .nav__popup {
    display: flex;
    max-height: max-content;
  }

  .menu-button {
    display: none;
  }

  .js .menu-button {
    display: none;
  }
}

@media (min-width: 1024px) {
  .nav__link {
    font-size: 1rem;
  }
}